import React from 'react'
import { Icon, InlineIcon } from '@iconify/react';
import reportIcon from '@iconify/icons-whh/report';
import bookmarkIcon from '@iconify/icons-fa/bookmark';
import binIcon from '@iconify/icons-icomoon-free/bin';
import appointmentsSolid from '@iconify-icons/teenyicons/appointments-solid';

function Folder() {
    return (
        <div className="folder">
            <div style={{ margin:"10px 10px 10px 50px" }}>
            <Icon icon={appointmentsSolid} style={{color: '#ffffff', fontSize: '20px',marginRight:"10px"}} />
            <span className="fold">Appointements</span>
            </div>
            <div style={{ margin:"55px 10px 10px 50px" }}>
            <Icon icon={reportIcon} style={{color: '#ffffff', fontSize: '20px',marginRight:"10px"}} />
            <span className="fold">Reports</span>
            </div>
            <div style={{ margin:"55px 10px 10px 50px" }}>
            <Icon icon={bookmarkIcon} style={{color: '#ffffff', fontSize: '20px',marginRight:"10px"}} />
            <span className="fold">Bookmarks</span>

            </div>
            <div style={{ margin:"55px 10px 10px 50px" }}>
            <Icon icon={binIcon} style={{color: '#ffffff', fontSize: '20px',marginRight:"10px"}} />
            <span className="fold">Bin</span>

            </div>
        </div>
    )
}
 
export default Folder
